import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import Home from '@/components/home'
import CreateSite from '@/components/createSite'
import HomeContent from '@/components/homeContent'
import SitesHome from '@/components/sites/sitesHome'
import Site from '@/components/sites/site'
import SiteElements from '@/components/sites/siteElements'
import SiteScripts from '@/components/sites/siteScripts'
import SiteSettings from '@/components/sites/siteSettings'
import SiteScript from '@/components/sites/siteScript'
import SetToken from '@/components/sites/setToken'

import VueChartkick from 'vue-chartkick'
import Chart from 'chart.js'

Vue.use(VueChartkick, {adapter: Chart})

Vue.use(Router)
Vue.use(VueResource)

export default new Router({
  mode: 'history',
  props: true,
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {path: 'createSite', name: 'createSite', component: CreateSite},
        {path: 'sites', name: 'sitesHome', component: SitesHome},
        {path: 'sites/:siteId', name: 'site', component: Site},
        {path: 'sites/:siteId/elements', name: 'siteElements', component: SiteElements},
        {path: 'sites/:siteId/sitescripts',
          name: 'siteScripts',
          component: SiteScripts,
          children: [{
            path: ':siteScriptId', name: 'siteScript', component: SiteScript
          }]
        },
        {path: 'sites/:siteId/settings', name: 'siteSettings', component: SiteSettings},
        {path: '', name: 'HomeContent', component: HomeContent}
      ]
    },
    {path: '/setToken', name: 'setToken', component: SetToken}
  ]
})



