<template>
  <div class="m-grid__item m-grid__item--fluid m-wrapper">
    <div class="m-content">
  <div class="m-subheader ">
    <div class="d-flex align-items-center">
      <div class="mr-auto">
        <ul class="m-subheader__breadcrumbs m-nav m-nav--inline">
          <li class="m-nav__item m-nav__item--home">
            <a href="/" class="m-nav__link m-nav__link--icon">
              <i class="m-nav__link-icon la la-home"></i>
            </a>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <router-link :to="{ name: 'sitesHome' }" class="m-menu__link ">
              <span class="m-nav__link-text">
                Сайты
              </span>
            </router-link>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <a href="#" class="m-nav__link">
              <span class="m-nav__link-text">
                {{site.name}}
              </span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="m-content">
      <!--begin::Portlet-->
      <div class="m-portlet m-portlet--mobile">
        <div class="m-portlet__head">
          <div class="m-portlet__head-caption">
            <div class="m-portlet__head-title">
              <span class="m-portlet__head-icon m--hide">
                <i class="la la-gear"></i>
              </span>
              <h3 class="m-portlet__head-text">
                {{site.name}}
              </h3>
            </div>
          </div>
          <div class="m-portlet__head-tools">
            <ul class="nav nav-pills nav-pills--brand m-nav-pills--align-right m-nav-pills--btn-pill m-nav-pills--btn-sm" role="tablist">
              <li>
                <multiselect v-model="selectedScript" placeholder="Все сценарии" label="name" track-by="name" :options="siteScripts" :multiple="false" @input="changeScript"></multiselect>
              </li>
              <li class="nav-item m-tabs__item">
                <a class="nav-link m-tabs__link active show" data-toggle="tab" href="#m_widget2_tab1_content" role="tab" aria-selected="true" @click="changeInterval('7daysAgo')">
                  За неделю
                </a>
              </li>
              <li class="nav-item m-tabs__item">
                <a class="nav-link m-tabs__link" data-toggle="tab" href="#m_widget2_tab2_content" role="tab" aria-selected="false" @click="changeInterval('30daysAgo')">
                  За месяц
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="m-portlet__body">
          <div class="row">
            <div class="col-md-2">
              <div class="m-widget1" style="padding-top: 0px;">
                  <div class="m-widget1__item">
                    <div class="row m-row--no-padding align-items-center">
                      <div class="col">
                        <h3 class="m-widget1__title">
                          Просмотров
                        </h3>
                      </div>
                      <div class="col m--align-right">
                        <span class="m-widget1__number m--font-brand">
                          {{data.pageviews}}
                        </span>
                      </div>
                    </div>
                  </div>
                  <div class="m-widget1__item">
                    <div class="row m-row--no-padding align-items-center">
                      <div class="col">
                        <h3 class="m-widget1__title">
                          Новых посетителей
                        </h3>
                      </div>
                      <div class="col m--align-right">
                        <span class="m-widget1__number m--font-danger">
                          {{data.newUsers}}
                        </span>
                      </div>
                    </div>
                  </div>
                  <div class="m-widget1__item">
                    <div class="row m-row--no-padding align-items-center">
                      <div class="col">
                        <h3 class="m-widget1__title">
                          Конверсия
                        </h3>
                        <span class="m-widget1__desc">
                          По всем целям
                        </span>
                      </div>
                      <div class="col m--align-right">
                        <span class="m-widget1__number m--font-success">
                          {{data.conversion}} %
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
            <div class="col-md-8">
                <line-chart :data="chartData" :messages="{empty: 'Нет данных, возможно не установлен id яндекс метрики'}" legend="bottom"></line-chart>
            </div>
            <div class="col-md-2">
              <div class="m-demo">
                  <div class="m-list-search">
                    <div class="m-list-search__results">
                      <span class="m-list-search__result-category" style="margin-top: 0px;">
                        Управление
                      </span>
                      <router-link :to="{ name: 'siteElements' }" class="m-list-search__result-item">
                        <span class="m-list-search__result-item-icon">
                          <i class="flaticon-edit-1 m--font-warning"></i>
                        </span>
                        <span class="m-list-search__result-item-text">
                          Разметить страницу
                        </span>
                      </router-link>
                      <router-link :to="{ name: 'siteScripts' }" class="m-list-search__result-item">
                        <span class="m-list-search__result-item-icon">
                          <i class="flaticon-web m--font-primary"></i>
                        </span>
                        <span class="m-list-search__result-item-text">
                          Настроить сценарии
                        </span>
                      </router-link>
                      <router-link :to="{ name: 'siteSettings' }" class="m-list-search__result-item">
                        <span class="m-list-search__result-item-icon">
                          <i class="flaticon-cogwheel-1 m--font-success"></i>
                        </span>
                        <span class="m-list-search__result-item-text">
                          Настройки сайта
                        </span>
                      </router-link>
                      <a v-bind:href="'https://metrika.yandex.ru/dashboard?id='+ site.idYaMetrika" target="_blank" class="m-list-search__result-item">
                        <span class="m-list-search__result-item-icon">
                          <i class="flaticon-diagram m--font-danger"></i>
                        </span>
                        <span class="m-list-search__result-item-text">
                          Яндекс Метрика
                        </span>
                      </a>
                    </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--end::Portlet-->
     </div>
    </div>
  </div>
</template>

<script>
import config from '../../config'
import Datepicker from 'vuejs-datepicker'
import Multiselect from 'vue-multiselect'

export default {
  components: {
    Datepicker,
    Multiselect
  },
  data () {
    return {
      site: {},
      chartData: [],
      inputData: [],
      data: [],
      emptyData: {},
      interval: '7daysAgo',
      siteScripts: [],
      selectedScript: null
    }
  },
  created: function () {
    this.fetchSiteData()
    this.fetchStatisticData()
  },
  methods: {
    fetchSiteData: function () {
      this.$http.get(config.api.uri + 'sites/' + this.$route.params.siteId + '/siteScripts').then((response) => {
        this.site = response.body
        this.siteScripts = response.body.siteScripts
      }, (response) => {

      })
    },
    fetchStatisticData: function () {
      this.$http.get(config.api.uri + 'getStatistic?siteId=' + this.$route.params.siteId + '&interval=' + this.interval).then((response) => {
        if (response.body.length > 0) {
          this.inputData = response.body
          this.chartData = [{name: 'Просмотры', data: {}}, {name: 'Новые посетители', data: {}}]
          let day1 = new Date(Date.now())
          let n = 8
          if (this.interval !== '7daysAgo') {
            n = 31
          }
          for (let i = 0; i < n; i++) {
            let nextDate = this.formatDate(day1)
            this.emptyData[nextDate] = 0
            let day2 = new Date(day1 - 864e5)
            day1 = day2
          }
          this.changeScript()
        }
      }, (response) => {
        console.log(response)
      })
    },
    formatDate (inputDate) {
      let dd = inputDate.getDate()
      let mm = inputDate.getMonth() + 1 // January is 0!
      let yyyy = inputDate.getFullYear()
      if (dd < 10) {
        dd = '0' + dd
      }
      if (mm < 10) {
        mm = '0' + mm
      }

      return yyyy + '-' + mm + '-' + dd
    },
    changeInterval: function (interval) {
      if (this.interval !== interval) {
        this.interval = interval
        this.emptyData = []
        this.fetchStatisticData()
      }
    },
    changeScript: function () {
      console.log(this.selectedScript)
      let pageview = 0
      let newUser = 0
      this.data.pageviews = 0
      this.data.newUsers = 0
      this.data.conversion = 0
      this.chartData[0].data = Object.assign({}, this.emptyData)
      this.chartData[1].data = Object.assign({}, this.emptyData)
      let flag = false
      if (this.selectedScript == null) {
        flag = true
      }
      for (let i = 0; i < this.inputData.length; i++) {
        if (flag || this.selectedScript.utm_term === this.inputData[i].dimensions[1].name) {
          pageview = this.inputData[i].metrics[0]
          newUser = this.inputData[i].metrics[1]
          this.data.conversion += this.inputData[i].metrics[2]
          this.data.pageviews += pageview
          this.data.newUsers += newUser
          this.chartData[0].data[this.inputData[i].dimensions[0].name] += pageview
          this.chartData[1].data[this.inputData[i].dimensions[0].name] += newUser
        }
      }
      this.data.conversion = (parseInt(this.data.conversion) / parseInt(this.data.pageviews)).toFixed(2)
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



