<template>
  <div class="m-grid__item m-grid__item--fluid m-wrapper">
    <div class="m-content">
  <div class="m-subheader ">
    <div class="d-flex align-items-center">
      <div class="mr-auto">
        <ul class="m-subheader__breadcrumbs m-nav m-nav--inline">
          <li class="m-nav__item m-nav__item--home">
            <a href="/" class="m-nav__link m-nav__link--icon">
              <i class="m-nav__link-icon la la-home"></i>
            </a>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <a href="#" class="m-nav__link">
              <span class="m-nav__link-text">
                Добавление нового сайта
              </span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="m-content">
      <!--begin::Portlet-->
      <div class="m-portlet m-portlet--mobile">
        <div class="m-portlet__head">
          <div class="m-portlet__head-caption">
            <div class="m-portlet__head-title">
              <span class="m-portlet__head-icon m--hide">
                <i class="la la-gear"></i>
              </span>
              <h3 class="m-portlet__head-text">
                Добавление нового сайта
              </h3>
            </div>
          </div>
        </div>
        <notification v-bind:notifications="notifications"></notification>
        <!--begin::Form-->
        <form class="m-form m-form--fit m-form--label-align-right" v-on:submit.prevent="createSite">
          <div class="m-portlet__body">
            <div class="form-group m-form__group">
              <label>
                Адрес сайта
              </label>
              <input type="text" required class="form-control m-input" v-model="site.url" placeholder="example.com">
            </div>
            <div class="form-group m-form__group">
              <label>
                Название сайта
              </label>
              <input type="text" required class="form-control m-input" v-model="site.name" placeholder="Проект 1">
            </div>
            <div class="form-group m-form__group">
              <label>
                Id Яндекс Метрики
              </label>
              <input type="text" class="form-control m-input" v-model="site.idYaMetrika" placeholder="id">
            </div>
            <!-- TODO v-else -->
            <!-- <div class="form-group m-form__group">
              <label>
                oath-token
              </label>
              <input type="text" class="form-control m-input" required v-model="site.directToken" placeholder="">
            </div> -->
            <div class="form-group m-form__group">
              <label for="exampleInputEmail1">
                Город
              </label>
              <div class="row">
                <div class="col-md-8">
                  <multiselect v-model="site.cityId" placeholder="Выберите город" label="name" track-by="name" :options="cities" :multiple="false"></multiselect>
                </div>
              </div>
            </div>
            <div class="form-group m-form__group">
              <label for="exampleInputEmail1">
                Ниша
              </label>
              <div class="row">
                <div class="col-md-8">
                <multiselect v-model="site.nicheId" placeholder="Выберите нишу" label="title" track-by="title" :options="niches" :multiple="false"></multiselect>
                </div>
                <div class="col-md-4">
                  <button class="btn btn-primary" type="button" data-toggle="modal" data-target="#createNiche">
                    Добавить нишу
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div class="m-portlet__foot m-portlet__foot--fit">
            <div class="m-form__actions">
              <button type="submit" class="btn btn-primary">
                Продолжить
              </button>
            </div>
          </div>
        </form>
        <!--end::Form-->
      </div>
      <!--end::Portlet-->
     </div>
    </div>
    <div class="modal fade show" id="createNiche" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" style="display:none;">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              Добавить нишу
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">
                ×
              </span>
            </button>
          </div>
          <div class="modal-body">
            <form class="m-form m-form--fit m-form--label-align-right">
              <div class="m-portlet__body">
                <div class="form-group m-form__group">
                  <label for="exampleInputEmail1">
                    Название
                  </label>
                  <input type="text" class="form-control m-input" required v-model="niche.title" placeholder="Название ниши">
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">
              Отмена
            </button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="createNiche">
              Добавить
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Notification from './notifications.vue'
import Multiselect from 'vue-multiselect'
import config from '../config'

export default {
  components: {
    Multiselect,
    Notification
  },
  data () {
    return {
      site: {},
      niches: [],
      cities: [],
      notifications: [],
      niche: {}
    }
  },
  created: function () {
    this.fetchNichesData()
    this.fetchCitiesData()
  },
  methods: {
    fetchNichesData: function () {
      this.$http.get(config.api.uri + 'niches').then((response) => {
        let niches = response.body
        this.niches = niches.map(function (niche) {
          return {'id': niche.id, 'title': niche.title} // TODO: OR NAME
        })
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось загрузить ниши, попробуйте перезагрузить страницу'
        })
      })
    },
    fetchCitiesData: function () {
      this.$http.get(config.api.uri + 'cities').then((response) => {
        let cities = response.body
        this.cities = cities.map(function (city) {
          return {'id': city.id, 'name': city.name}
        })
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось загрузить города, попробуйте перезагрузить страницу'
        })
      })
    },
    createSite: function () {
      console.log('q')
      console.log(this.site)
      this.site.cityId = this.site.cityId.id
      this.site.nicheId = this.site.nicheId.id
      this.$http.post(config.api.uri + 'sites/', this.site, {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        // let niche = response.body
        // this.niches.push({'id': niche.id, 'title': niche.title})
        // this.site.nicheId = this.niches[this.niches.length - 1]
        this.$router.push({name: 'siteElements', params: { siteId: response.body.id }})
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Произошла ошибка, сайт не добавлен. Проверьте данные и попробуйте еще раз'
        })
      })
    },
    createNiche: function () {
      this.$http.post(config.api.uri + 'niches/', this.niche, {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        let niche = response.body
        this.niches.push({'id': niche.id, 'title': niche.title})
        this.site.nicheId = this.niches[this.niches.length - 1]
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Произошла ошибка, ниша не создана. Возможно такая ниша уже есть'
        })
      })
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



