<template>
  <div class="m-grid__item m-grid__item--fluid m-wrapper">
    <div class="m-content">
  <div class="m-subheader ">
    <div class="d-flex align-items-center">
      <div class="mr-auto">
        <ul class="m-subheader__breadcrumbs m-nav m-nav--inline">
          <li class="m-nav__item m-nav__item--home">
            <a href="/" class="m-nav__link m-nav__link--icon">
              <i class="m-nav__link-icon la la-home"></i>
            </a>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <router-link :to="{ name: 'sitesHome' }" class="m-menu__link ">
              <span class="m-nav__link-text">
                Сайты
              </span>
            </router-link>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <router-link :to="{ name: 'site', params: {siteId: site.id}}" class="m-menu__link ">
              <span class="m-nav__link-text">
                {{site.name}}
              </span>
            </router-link>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <a href="#" class="m-nav__link">
              <span class="m-nav__link-text">
                Настроить сценарии
              </span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="m-content">
    <div class="row">
      <div class="col-xl-3 col-lg-12">
        <div class="m-portlet m-portlet--mobile">
        <div class="m-portlet__head">
          <div class="m-portlet__head-caption">
            <div class="m-portlet__head-title">
              <span class="m-portlet__head-icon m--hide">
                <i class="la la-gear"></i>
              </span>
              <h3 class="m-portlet__head-text">
                Сценарии
              </h3>
            </div>
          </div>
           <div class="m-portlet__head-tools">
            <button id="btn_add_elements" class="btn btn-success" type="button" data-toggle="modal" data-target="#createSiteScript" @click="clearModal">
              Добавить
            </button>
          </div>
        </div>
        <div class="m-portlet__body">
          <notification v-bind:notifications="notifications"></notification>
              <div id="table" class="table table-editable overflow block_elements grid-body no-border">
                <div class="m-section__content">
                  <table class="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Название</th>
                        <th>Действия</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(siteScript, index) in site.siteScripts" :key="siteScript.id">
                        <td scope="row">{{(index+1)}}</td>
                        <td><router-link :to="{name: 'siteScript', params: {siteScriptId: siteScript.id}}">{{siteScript.name}}</router-link></td>
                        <td>
                          <a class="m-portlet__nav-link btn m-btn m-btn--hover-accent m-btn--icon m-btn--icon-only m-btn--pill" title="Edit details" data-toggle="modal" data-target="#createSiteScript" @click="editSiteScript(siteScript)">
                            <i class="la la-edit"></i>
                          </a>
                          <a class="m-portlet__nav-link btn m-btn m-btn--hover-danger m-btn--icon m-btn--icon-only m-btn--pill" title="Delete" @click="removeSiteScript(siteScript)">
                            <i class="la la-trash"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
            </div>
        </div>
        </div>
      </div>
          <div class="col-xl-9 col-lg-12">
            <router-view/>
          </div>
    </div>
     </div>
    </div>
    <div class="modal fade show" id="createSiteScript" tabindex="-1" role="dialog" style="display:none;">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              Добавить/изменить сценарий
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">
                ×
              </span>
            </button>
          </div>
          <div class="modal-body">
            <form class="m-form m-form--fit m-form--label-align-right">
              <div class="m-portlet__body">
                <div class="form-group m-form__group">
                  <label>
                    Название
                  </label>
                  <input type="text" class="form-control m-input" required v-model="siteScript.name" placeholder="Новый сценарий">
                </div>
                <div class="form-group m-form__group">
                  <label>
                    utm_term
                  </label>
                  <input type="text" class="form-control m-input" required v-model="siteScript.utm_term" placeholder="test">
                </div>
                <div v-if="site.oauthTokenYandex !== ''" class="form-group m-form__group">
                  <label>Группа объявлений Яндекс Директ</label>
                  <multiselect v-model="siteScript.directGroupId" placeholder="Выберите группу" label="name" track-by="name" :options="directGroups" :multiple="false"></multiselect>
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">
              Отмена
            </button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="addSiteScript">
              Сохранить
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from '../../config'
import editableTd from '../editable-td'
import Notification from '../notifications.vue'
import Multiselect from 'vue-multiselect'

export default {
  data () {
    return {
      site: {
        siteScripts: [],
        siteElements: []
      },
      siteScript: {},
      notifications: [],
      directGroups: []
    }
  },
  created: function () {
    this.fetchData()
  },
  methods: {
    fetchData: function () {
      this.$http.get(config.api.uri + 'sites/' + this.$route.params.siteId + '/siteScripts').then((response) => {
        this.site = response.body
        this.fetchDirectGroups()
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось загрузить информацию'
        })
      })
    },
    fetchDirectGroups: function () {
      if (this.site.oauthTokenYandex !== '') {
        this.$http.get(config.api.uri + 'getDirectGroups/' + this.site.id).then((response) => {
          response.body.map(c => this.directGroups.push(this.createDirectObject(c)))
        }, (response) => {
          this.notifications.push({
            type: 'danger',
            message: 'Не удалось загрузить группы объявлений из Яндекс Директа'
          })
        })
      }
    },
    addSiteScript: function () {
      this.siteScript.siteId = this.site.id
      console.log(this.siteScript.directGroupId)
      if (typeof this.siteScript.directGroupId !== 'undefined') {
        this.siteScript.directGroupId = this.siteScript.directGroupId.id
      }
      if (!this.siteScript.id) {
        this.$http.post(config.api.uri + 'siteScripts/', this.siteScript).then((response) => {
          this.site.siteScripts.push(response.body)
          if (typeof response.body.directGroupId !== 'undefined') {
            this.setUtms(response.body.id)
          }
        }, (response) => {
          this.notifications.push({
            type: 'danger',
            message: 'Не удалось создать'
          })
        })
      } else {
        this.$http.patch(config.api.uri + 'siteScripts/' + this.siteScript.id, this.siteScript).then((response) => {
          if (typeof response.body.directGroupId !== 'undefined') {
            this.setUtms(response.body.id)
          }
        }, (response) => {
          this.notifications.push({
            type: 'danger',
            message: 'Не удалось изменить'
          })
        })
      }
    },
    clearModal: function () {
      this.siteScript = {}
    },
    editSiteScript: function (siteScript) {
      this.siteScript = siteScript
      this.setDirectGroup()
    },
    removeSiteScript: function (siteScript) {
      console.log(siteScript)
      if (confirm('Вы действительно хотите удалить сценарий ' + siteScript.name + '?')) {
        this.$http.delete(config.api.uri + 'siteScripts/' + siteScript.id, {
          headers: {
            'Content-Type': 'application/json'
          }
        }).then((response) => {
          for (let j = 0; j < this.site.siteScripts.length; j++) {
            if (this.site.siteScripts[j].id === siteScript.id) {
              this.site.siteScripts.splice(j, 1)
              return
            }
          }
        }, (response) => {
          this.notifications.push({
            type: 'danger',
            message: 'Не удалось удалить'
          })
        })
      }
    },
    setDirectGroup: function () {
      for (let i = 0; i < this.directGroups.length; i++) {
        if (this.siteScript.directGroupId === this.directGroups[i].id) {
          this.siteScript.directGroupId = this.directGroups[i]
          return
        }
      }
    },
    createDirectObject: function (c) {
      return {name: c.Name + ' [' + c.Id + ']', id: '' + c.Id}
    },
    setUtms: function (id) {
      this.$http.post(config.api.uri + 'setUtm/' + id).then((response) => {
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось проставить utm-метки'
        })
      })
    }
  },
  components: {
    'editable-td': editableTd,
    Notification,
    Multiselect
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



