<template>
  <div class="m-portlet m-portlet--mobile">
    <div class="m-portlet__head">
      <div class="m-portlet__head-caption">
        <div class="m-portlet__head-title">
          <span class="m-portlet__head-icon m--hide">
            <i class="la la-gear"></i>
          </span>
          <h3 class="m-portlet__head-text">
            {{siteScript.name}}
          </h3>
        </div>
      </div>
        <div class="m-portlet__head-tools">
          <button type="button" class="btn btn-primary">
            Показать объявления
          </button>
          <button class="btn btn-success" type="button" @click="addScriptVersion">
            Добавить вариант
          </button>
        </div>
    </div>
    <div class="m-portlet__body">
      <notification v-bind:notifications="notifications"></notification>
      <!-- <p>{{siteScript}}</p>
      <p>{{siteElements}}</p> -->
      <div v-if="ads.length > 0">
        <h4>Объявления</h4>
        <div class="row" style="padding-bottom:10px">
          <div class="col-md-4" v-for="ad in ads" :key="ad.id" style="margin-left:15px; border: 4px solid #f7f7fa;">
            <h5>{{ad.TextAd.Title}}</h5>
            <p>{{ad.TextAd.Text}}</p>
          </div>
        </div>
      </div>
      <div id="table" class="table table-editable overflow block_elements grid-body no-border">
        <table class="table table-bordered" id="table_elements">
          <tr class="tr-head">
          <!-- <th>id</th> -->
          <th v-for="siteElement in siteElements" :key="siteElement.id">
            {{siteElement.name}}
          </th>
          <th>Действия</th>
          </tr>
          <tbody>
          <tr v-for="scriptVersion in siteScript.scriptVersions" :key="scriptVersion.id">
              <!-- <td>{{scriptVersion.isActive}}</td> -->
              <td v-for="elementValue in scriptVersion.elementsValue" :key="elementValue.id">
                <div class="tab-content">
                  <div class="tab-pane active" id="m_tabs_1_1" role="tabpanel">
                    <div class="form-group m-form__group">
                      <textarea name="" id="" v-model="elementValue.value" :disabled="elementValue.isEmpty"></textarea><br>
                      <label class="m-checkbox">
                        <input type="checkbox" v-model="elementValue.isEmpty">Оставить пустым<span></span>
                      </label>
                    </div>
                  </div>
                </div>
              </td>
              <td>
                <a class="m-portlet__nav-link btn m-btn m-btn--hover-danger m-btn--icon m-btn--icon-only m-btn--pill" title="Удалить" @click="removeScriptVersion(scriptVersion)">
                  <i class="la la-trash"></i>
                </a>
                <a v-if="scriptVersion.isActive" class="m-portlet__nav-link btn m-btn m-btn--hover-warning m-btn--icon m-btn--icon-only m-btn--pill" title="Остановить" @click="changeActive(scriptVersion)">
                  <i class="la la-pause"></i>
                </a>
                <a v-else class="m-portlet__nav-link btn m-btn m-btn--hover-success m-btn--icon m-btn--icon-only m-btn--pill" title="Запустить" @click="changeActive(scriptVersion)">
                  <i class="la la-play"></i>
                </a>
                <br>
                <button class="btn btn-primary" @click="updateScriptVersion(scriptVersion)">Сохранить</button>
              </td>
          </tr>
          </tbody>
      </table>
    </div>
    </div>
    </div>
  <!--end::Portlet-->
</template>

<script>
import config from '../../config'
import editableTd from '../editable-td'
import Notification from '../notifications.vue'

export default {
  data () {
    return {
      siteScript: {},
      notifications: [],
      siteElements: [],
      ads: []
    }
  },
  created: function () {
    this.fetchData()
    this.fetchSiteElementsData()
  },
  methods: {
    fetchData: function () {
      this.$http.get(config.api.uri + 'siteScripts/' + this.$route.params.siteScriptId).then((response) => {
        this.siteScript = response.body
        if (typeof this.siteScript.directGroupId !== 'undefined') {
          this.fethAdsData()
        }
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось загрузить информацию о сценарии'
        })
      })
    },
    fetchSiteElementsData: function () {
      this.$http.get(config.api.uri + 'sites/' + this.$route.params.siteId + '/siteElements').then((response) => {
        this.siteElements = response.body
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось загрузить информацию о элементах'
        })
      })
    },
    fethAdsData: function () {
      this.$http.get(config.api.uri + 'getAds/' + this.siteScript.id).then((response) => {
        this.ads = response.body
      }, (response) => {
        this.notifications.push({
          type: 'warning',
          message: 'Не удалось загрузить информацию о объявлениях'
        })
      })
    },
    addScriptVersion: function () {
      this.$http.post(config.api.uri + 'scriptVersions/', {siteId: this.$route.params.siteId, siteScriptId: this.siteScript.id}).then((response) => {
        this.siteScript.scriptVersions.push(response.body)
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось создать'
        })
      })
    },
    removeScriptVersion: function (scriptVersion) {
      if (confirm('Вы действительно хотите удалить эту версию?')) {
        this.$http.delete(config.api.uri + 'scriptVersions/' + scriptVersion.id, {
          headers: {
            'Content-Type': 'application/json'
          }
        }).then((response) => {
          for (let j = 0; j < this.siteScript.scriptVersions.length; j++) {
            if (this.siteScript.scriptVersions[j].id === scriptVersion.id) {
              this.siteScript.scriptVersions.splice(j, 1)
              return
            }
          }
        }, (response) => {
          this.notifications.push({
            type: 'danger',
            message: 'Не удалось удалить'
          })
        })
      }
    },
    changeActive: function (scriptVersion) {
      scriptVersion.isActive = !scriptVersion.isActive
    },
    updateScriptVersion: function (scriptVersion) {
      this.$http.patch(config.api.uri + 'scriptVersions/' + scriptVersion.id, scriptVersion).then((response) => {
        this.notifications.push({
          type: 'success',
          message: 'Данные успешно обновлены'
        })
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось изменить'
        })
      })
    }
  },
  components: {
    'editable-td': editableTd,
    Notification
  },
  beforeRouteUpdate (to, from, next) {
    // вызывается когда маршрут, что рендерит этот компонент изменился,
    // но этот компонент будет повторно использован в новом маршруте.
    // Например, для маршрута с динамическими параметрами `/foo/:id`, когда мы
    // перемещаемся между `/foo/1` и `/foo/2`, экземпляр того же компонента `Foo`
    // будет использован повторно, и этот хук будет вызван когда это случится.
    // Также имеется доступ в `this` к экземпляру компонента.
    next()
    this.ads = []
    this.notifications = []
    this.fetchData()
  }
}
</script>



