<template>
  <div class="m-grid__item m-grid__item--fluid m-wrapper">
    <div class="m-content">
  <div class="m-subheader ">
    <div class="d-flex align-items-center">
      <div class="mr-auto">
        <ul class="m-subheader__breadcrumbs m-nav m-nav--inline">
          <li class="m-nav__item m-nav__item--home">
            <a href="/" class="m-nav__link m-nav__link--icon">
              <i class="m-nav__link-icon la la-home"></i>
            </a>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <a href="#" class="m-nav__link">
              <span class="m-nav__link-text">
                Сайты
              </span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="m-content">
      <!--begin::Portlet-->
      <div class="m-portlet m-portlet--mobile">
        <div class="m-portlet__head">
          <div class="m-portlet__head-caption">
            <div class="m-portlet__head-title">
              <span class="m-portlet__head-icon m--hide">
                <i class="la la-gear"></i>
              </span>
              <h3 class="m-portlet__head-text">
                Сайты
              </h3>
            </div>
          </div>
        </div>
        <div class="m-portlet__body">
          <!--begin::Section-->
          <div class="m-section">
            <div v-for="niche in niches"
            :key="niche.id" v-if="niche.sites.length > 0">
              <h3 class="m-section__heading">
                {{niche.title}}
              </h3>
              <div class="m-section__content">
                <div class="m-demo" data-code-preview="true" data-code-html="true" data-code-js="false">
                  <div class="m-demo__preview">
                    <p v-for="site in niche.sites" :key="site.id">
                      <router-link :to="{name: 'site', params: { siteId: site.id }}">{{site.name}}</router-link>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!--end::Section-->
        </div>
      </div>
      <!--end::Portlet-->
     </div>
    </div>
  </div>
</template>

<script>
import config from '../../config'

export default {
  data () {
    return {
      niches: []
    }
  },
  created: function () {
    this.fetchNichesData()
  },
  methods: {
    fetchNichesData: function () {
      this.$http.get(config.api.uri + 'nichesWithSites').then((response) => {
        this.niches = response.body
      }, (response) => {
      })
    }
  }
}
</script>



