<template>
  <div class="m-grid__item m-grid__item--fluid m-wrapper">
    <div class="m-content">
  <div class="m-subheader ">
    <div class="d-flex align-items-center">
      <div class="mr-auto">
        <ul class="m-subheader__breadcrumbs m-nav m-nav--inline">
          <li class="m-nav__item m-nav__item--home">
            <a href="/" class="m-nav__link m-nav__link--icon">
              <i class="m-nav__link-icon la la-home"></i>
            </a>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <router-link :to="{ name: 'sitesHome' }" class="m-menu__link ">
              <span class="m-nav__link-text">
                Сайты
              </span>
            </router-link>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <router-link :to="{ name: 'site', params: {siteId: site.id}}" class="m-menu__link ">
              <span class="m-nav__link-text">
                {{site.name}}
              </span>
            </router-link>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <a href="#" class="m-nav__link">
              <span class="m-nav__link-text">
                Разметить страницу
              </span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="m-content">
      <!--begin::Portlet-->
      <div class="m-portlet m-portlet--mobile">
        <div class="m-portlet__head">
          <div class="m-portlet__head-caption">
            <div class="m-portlet__head-title">
              <span class="m-portlet__head-icon m--hide">
                <i class="la la-gear"></i>
              </span>
              <h3 class="m-portlet__head-text">
                Разметить страницу
              </h3>
            </div>
          </div>
          <div class="m-portlet__head-tools">
            <button class="table-add btn btn-success" type="button" id="btn_add_elements" @click="addNewLine">
              Добавить
            </button>
          </div>
        </div>
        <div class="m-portlet__body">
          <notification v-bind:notifications="notifications"></notification>
              <div id="table" class="table table-editable overflow block_elements grid-body no-border">
                <!-- <span class="table-add pull-right btn btn-success" id="btn_add_elements" @click="addNewLine">Добавить</span> -->
                <!-- <i class="btn table-add pull-right flaticon-plus" id="btn_add_elements" @click="addNewLine"></i> -->
                <table class="table" id="table_elements">
                    <tr class="tr-head">
                    <th>Название</th>
                    <th>Ключ</th>
                    <th></th>
                    </tr>
                    <tbody>
                    <tr v-for="(element) in elements"
                    :key="element.id">
                        <editable-td :content="element.name" @update="element.name = $event"></editable-td>
                        <editable-td :content="element.keyName" @update="element.keyName = $event"></editable-td>
                        <td>
                            <div style="cursor: pointer" class="table-remove m-demo-icon__preview" @click="removeLine(element)">
                              <i class="la la-close"></i>
                          </div>
                        </td>
                    </tr>
                    </tbody>
                </table>
                <div class="form-actions" style="padding-bottom: 10px;">
                  <div class="pull-right">
                    <button type="submit" class="btn btn-primary btn-cons-md" @click="saveElements">Сохранить</button>
                  </div>
                </div>
            </div>
          </div>
        </div>
      <!--end::Portlet-->
     </div>
    </div>
  </div>
</template>

<script>
import config from '../../config'
import editableTd from '../editable-td'
import Notification from '../notifications.vue'

let i = 0
export default {
  data () {
    return {
      site: {},
      elements: [],
      notifications: []
    }
  },
  created: function () {
    this.fetchSiteData()
    this.fetchSiteElementsData()
  },
  methods: {
    fetchSiteData: function () {
      this.$http.get(config.api.uri + 'sites/' + this.$route.params.siteId).then((response) => {
        this.site = response.body
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось получить инфрмацию о сайте'
        })
      })
    },
    fetchSiteElementsData: function () {
      this.$http.get(config.api.uri + 'sites/' + this.$route.params.siteId + '/siteElements').then((response) => {
        this.elements = response.body
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось получить данные о элементах'
        })
      })
    },
    saveElements: function () {
      console.log(this.elements)
      this.$http.post(config.api.uri + 'sites/' + this.$route.params.siteId + '/saveSiteElements/', this.elements, {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        this.$router.push({name: 'siteScripts', params: { siteId: this.site.id }})
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось сохранить данные'
        })
      })
    },
    back: function () {
      this.$router.go(-1)
    },
    addNewLine: function () {
      this.elements.push({id: i.toString(), name: 'undef', keyName: 'undef', siteId: this.site.id})
      i++
    },
    removeLine: function (element) {
      if (element.id.length > 10) {
        if (confirm('Вы действительно хотите элемент ' + element.name + '?')) {
          this.$http.delete(config.api.uri + 'siteElements/' + element.id, {
            headers: {
              'Content-Type': 'application/json'
            }
          }).then((response) => {
          }, (response) => {
            console.log(response)
          })
        } else { return }
      }
      for (let j = 0; j < this.elements.length; j++) {
        if (this.elements[j].id === element.id) {
          this.elements.splice(j, 1)
          return
        }
      }
    }
  },
  components: {
    'editable-td': editableTd,
    Notification
  }
}
</script>



