<template>
  <div class="m-grid__item m-grid__item--fluid m-wrapper">
    <div class="m-content">
  <div class="m-subheader ">
    <div class="d-flex align-items-center">
      <div class="mr-auto">
        <ul class="m-subheader__breadcrumbs m-nav m-nav--inline">
          <li class="m-nav__item m-nav__item--home">
            <a href="/" class="m-nav__link m-nav__link--icon">
              <i class="m-nav__link-icon la la-home"></i>
            </a>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <router-link :to="{ name: 'sitesHome' }" class="m-menu__link ">
              <span class="m-nav__link-text">
                Сайты
              </span>
            </router-link>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <router-link :to="{ name: 'site', params: {siteId: site.id}}" class="m-menu__link ">
              <span class="m-nav__link-text">
                {{site.name}}
              </span>
            </router-link>
          </li>
          <li class="m-nav__separator">
            -
          </li>
          <li class="m-nav__item">
            <a href="#" class="m-nav__link">
              <span class="m-nav__link-text">
                Настройки
              </span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="m-content">
      <!--begin::Portlet-->
      <div class="m-portlet m-portlet--mobile">
        <div class="m-portlet__head">
          <div class="m-portlet__head-caption">
            <div class="m-portlet__head-title">
              <span class="m-portlet__head-icon m--hide">
                <i class="la la-gear"></i>
              </span>
              <h3 class="m-portlet__head-text">
                Настройки сайта
              </h3>
            </div>
          </div>
          <div class="m-portlet__head-tools">
            <button @click="removeSite" class="btn btn-danger">
              Удалить сайт
            </button>
          </div>
        </div>
        <div class="m-portlet__body">
        <notification v-bind:notifications="notifications"></notification>
        <button v-if="site.oauthTokenYandex === ''" class="btn btn-warning" @click="getToken">Яндекс Директ</button>
        <!--begin::Form-->
            <form class="m-form m-form--fit m-form--label-align-right" v-on:submit.prevent="updateSite">
            <div class="m-portlet__body">
                <div class="form-group m-form__group">
                <label>
                    Адрес сайта
                </label>
                <input type="text" v-model="site.url" required class="form-control m-input" placeholder="example.com">
                </div>
                <div class="form-group m-form__group">
                <label>
                    Название сайта
                </label>
                <input type="text" required class="form-control m-input" v-model="site.name" placeholder="Проект 1">
                </div>
                <div v-if="site.oauthTokenYandex !== ''" class="form-group m-form__group">
                  <label>Яндекс Директ</label>
                  <div class="row">
                    <div class="col-md-8">
                      <multiselect v-model="site.directCompanyId" placeholder="Выберите компанию" label="name" track-by="name" :options="directCopmpany" :multiple="false"></multiselect>
                    </div>
                    <div class="col-md-4">
                      <button class="btn btn-warning" type="button" @click="removeToken">
                          Удалить директ
                      </button>
                    </div>
                  </div>
                </div>
                <div class="form-group m-form__group">
                  <label>
                      Id Яндекс Метрики
                  </label>
                  <input type="text" class="form-control m-input" v-model="site.idYaMetrika" placeholder="id">
                </div>
                <div class="form-group m-form__group">
                  <label>
                      Token
                  </label>
                  <input type="text" class="form-control m-input" v-model="site.token" disabled placeholder="id">
                </div>
                <div class="form-group m-form__group">
                <label for="exampleInputEmail1">
                    Город
                </label>
                <div class="row">
                    <div class="col-md-8">
                      <multiselect v-model="site.cityId" placeholder="Выберите город" label="name" track-by="name" :options="cities" :multiple="false"></multiselect>
                    </div>
                </div>
                </div>
                <div class="form-group m-form__group">
                <label for="exampleInputEmail1">
                    Ниша
                </label>
                <div class="row">
                    <div class="col-md-8">
                    <multiselect v-model="site.nicheId" placeholder="Выберите нишу" label="title" track-by="title" :options="niches" :multiple="false"></multiselect>
                    </div>
                    <div class="col-md-4">
                    <button class="btn btn-primary" type="button" data-toggle="modal" data-target="#createNiche">
                        Добавить нишу
                    </button>
                    </div>
                </div>
                </div>
            </div>
            <div class="m-portlet__foot m-portlet__foot--fit">
                <div class="m-form__actions">
                <button type="submit" class="btn btn-primary">
                    Сохранить
                </button>
                </div>
            </div>
            </form>
            <!--end::Form-->
          </div>
        </div>
      <!--end::Portlet-->
     </div>
    </div>
    <div class="modal fade show" id="createNiche" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" style="display:none;">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              Добавить нишу
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">
                ×
              </span>
            </button>
          </div>
          <div class="modal-body">
            <form class="m-form m-form--fit m-form--label-align-right">
              <div class="m-portlet__body">
                <div class="form-group m-form__group">
                  <label for="exampleInputEmail1">
                    Название
                  </label>
                  <input type="text" class="form-control m-input" required v-model="niche.title" placeholder="Название ниши">
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">
              Отмена
            </button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="createNiche">
              Добавить
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from '../../config'
import Notification from '../notifications.vue'
import Modal from '../modalWindow.vue'
import Multiselect from 'vue-multiselect'

export default {
  data () {
    return {
      site: {
      },
      notifications: [],
      niches: [],
      cities: [],
      niche: {},
      directCopmpany: []
    }
  },
  created: function () {
    this.fetchSiteData()
    this.fetchNichesData()
    this.fetchCitiesData()
  },
  methods: {
    fetchSiteData: function () {
      this.$http.get(config.api.uri + 'sites/' + this.$route.params.siteId).then((response) => {
        this.site = response.body
        this.fetchDirectData()
      }, (response) => {
      })
      this.$http.get(config.api.uri + 'sites/' + this.$route.params.siteId + '/siteElements').then((response) => {
        this.elements = response.body
      }, (response) => {
      })
    },
    fetchNichesData: function () {
      this.$http.get(config.api.uri + 'niches').then((response) => {
        let niches = response.body
        this.niches = niches.map(function (niche) {
          return {'id': niche.id, 'title': niche.title}
        })
        this.setNiche()
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось загрузить ниши, попробуйте перезаугрузить страницу'
        })
      })
    },
    fetchCitiesData: function () {
      this.$http.get(config.api.uri + 'cities').then((response) => {
        let cities = response.body
        this.cities = cities.map(function (city) {
          return {'id': city.id, 'name': city.name}
        })
        this.setCity()
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Не удалось загрузить города, попробуйте перезаугрузить страницу'
        })
      })
    },
    fetchDirectData: function () {
      if (this.site.oauthTokenYandex !== '') {
        this.$http.get(config.api.uri + 'getCompany/' + this.site.id).then((response) => {
          response.body.map(c => this.directCopmpany.push(this.createDirectObject(c)))
          this.setCompany()
        }, (response) => {
          this.notifications.push({
            type: 'danger',
            message: 'Не удалось загрузить компании из Яндекс Директа'
          })
        })
      }
    },
    updateSite: function () {
      this.site.cityId = this.site.cityId.id
      this.site.nicheId = this.site.nicheId.id
      this.site.directCompanyId = this.site.directCompanyId.id
      this.$http.patch(config.api.uri + 'sites/' + this.site.id, this.site, {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        // this.setNiche()
        // this.setCity()
        // this.notifications.push({
        //   type: 'success',
        //   message: 'Информация обновлена'
        // })
        this.$router.push({name: 'site', params: { siteId: this.site.id }})
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Произошла ошибка, сайт не добавлен. Проверьте данные и попробуйте еще раз'
        })
      })
    },
    createNiche: function () {
      this.$http.post(config.api.uri + 'niches/', this.niche, {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        let niche = response.body
        this.niches.push({'id': niche.id, 'title': niche.title})
        this.site.nicheId = this.niches[this.niches.length - 1]
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Произошла ошибка, ниша не создана. Возможно такая ниша уже есть'
        })
      })
    },
    removeSite: function () {
      if (confirm('Вы действительно хотите удалить сайт ' + this.site.name + '?')) {
        this.$http.delete(config.api.uri + 'sites/' + this.$route.params.siteId, {
          headers: {
            'Content-Type': 'application/json'
          }
        }).then((response) => {
          this.$router.push({
            name: 'sitesHome'
          })
        }, (response) => {
          this.notifications.push({
            type: 'danger',
            message: 'Произошла ошибка, сайт не удален, попробуйте перезагрузить страницу'
          })
        })
      }
    },
    setNiche: function () {
      for (let i = 0; i < this.niches.length; i++) {
        if (this.site.nicheId === this.niches[i].id) {
          this.site.nicheId = this.niches[i]
          return
        }
      }
    },
    setCity: function () {
      for (let i = 0; i < this.cities.length; i++) {
        if (this.site.cityId === this.cities[i].id) {
          this.site.cityId = this.cities[i]
          return
        }
      }
    },
    setCompany: function () {
      for (let i = 0; i < this.directCopmpany.length; i++) {
        if (this.site.directCompanyId === this.directCopmpany[i].id) {
          this.site.directCompanyId = this.directCopmpany[i]
          return
        }
      }
    },
    getToken: function () {
      let newWin = window.open('https://oauth.yandex.ru/authorize?response_type=code&client_id=0031c3e64b3b4bbca25db4623c21834d&force_confirm=yes&ncrnd=522689&status=ok', 'Получить доступ', 'width=600,height=400')
      let checkInterval = () => {
        if (newWin.closed) {
          clearInterval(intervalID)
        }
        if (newWin.location.href.search(/http:\/\/localhost:8080\//) !== -1) {
          let href = newWin.location.href.split('=')
          console.log(href)
          newWin.close()
          if (href[1] !== '') {
            this.site.oauthTokenYandex = href[1]
            this.setYandexToken()
          }
        }
      }
      let intervalID = setInterval(checkInterval, 1000)
    },
    setYandexToken: function () {
      this.$http.patch(config.api.uri + 'setYandexToken/' + this.site.id, {oauthTokenYandex: this.site.oauthTokenYandex}, {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        this.fetchDirectData()
      }, (response) => {
        this.notifications.push({
          type: 'danger',
          message: 'Произошла ошибка, Яндекс Директ не подключен'
        })
      })
    },
    removeToken: function () {
      this.site.oauthTokenYandex = ''
      this.directCopmpany = []
      this.setYandexToken()
    },
    createDirectObject: function (c) {
      return {name: c.Name + ' [' + c.Id + ']', id: '' + c.Id}
    }
  },
  components: {
    Notification,
    Modal,
    Multiselect
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



